function transformBreakdowns(breakdowns) {
	if (!breakdowns) return;

	return breakdowns.map(breakdown => {
		return {
			sysId: breakdown.sys_id,
			elements: [...breakdown.element_sys_ids]
		};
	});
}

export function transformUUID(uuid) {
	// FIXME: The format should follow the multi element uuid format of the charts api
	return {
		indicatorSysid: uuid.indicator || '',
		breakdowns: transformBreakdowns(uuid.breakdowns) || '',
		domain: uuid.domain || '',
		aggregate: uuid.aggregate || ''
	};
}
