/**
 * Determines if chart response contains applied filters in it.
 * @param {*} chartData - obtained from CHARTDATA_LOADED action.
 *
 * - If 1 or more filters apply to the visual -> show icon with number of applied
 * - If 1 or more filters apply to the visual, but some are not applied -> show icon with number of applied (more info about not applied in the pop-over)
 * - If none filters apply to the visual, but some are not applied -> show filter icon without a number (more info about not applied in the pop-over)
 * - If the visual doesn't follow filters -> show no icon
 * - If no filter is selected by user -> show no icon
 */
export function getFiltersInfo(chartData, filterConfigurations) {
	const filtersInfo = {
		applied: [],
		notApplied: [],
		showIcon: false,
		showAppliedCount: false
	};

	if (!filterConfigurations || filterConfigurations.length === 0) {
		// The chart is configured as not to follow filters
		// or filters are missing during config
		// or user did not select any filters
		return filtersInfo;
	}

	const allApplied = [];
	const allNotApplied = [];

	for (const propName in chartData.filters) {
		if (chartData.filters[propName]) {
			const applied = chartData.filters[propName].applied;
			if (applied && Array.isArray(applied)) {
				allApplied.push(...applied);
			}
			const notApplied = chartData.filters[propName].notApplied;
			if (notApplied && Array.isArray(notApplied)) {
				allNotApplied.push(...notApplied);
			}
		}
	}

	for (const appliedItem of allApplied) {
		if (filtersInfo.applied.some(x => x.order === appliedItem.order)) {
			// Ignore it if we already have this filter added - happens for multi-series charts
			continue;
		}

		const filterItemConfig = filterConfigurations.find(
			x => x.order === appliedItem.order
		);

		if (filterItemConfig) {
			filtersInfo.applied.push({
				order: appliedItem.order,
				label: filterItemConfig.label || filterItemConfig.apply_to[0]
			});
		}
	}

	for (const notAppliedItem of allNotApplied) {
		if (
			filtersInfo.applied.some(x => x.order === notAppliedItem.order) ||
			filtersInfo.notApplied.some(x => x.order === notAppliedItem.order)
		) {
			// Ignore it if we already have this filter added - happens for multi-series charts
			continue;
		}

		const filterItemConfig = filterConfigurations.find(
			x => x.order === notAppliedItem.order
		);
		if (filterItemConfig) {
			let isValid = true;
			if (
				Array.isArray(filterItemConfig.values) &&
				filterItemConfig.values.length === 0
			) {
				/**
				 * We want to ignore this not applied filter in case user selected nothing and we have empty array
				 * Probably we should fix our backend but this is such a big pain
				 */
				isValid = false;
			}

			if (isValid) {
				const notAppliedInfo = {
					reason: notAppliedItem.reason,
					order: notAppliedItem.order,
					label: filterItemConfig.label || filterItemConfig.apply_to[0]
				};
				filtersInfo.notApplied.push(notAppliedInfo);
			}
		}
	}

	filtersInfo.showAppliedCount =
		filtersInfo.applied.length > 0 || filtersInfo.notApplied.length > 0;
	filtersInfo.showIcon =
		filtersInfo.applied.length > 0 || filtersInfo.notApplied.length > 0;

	return filtersInfo;
}
