import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';
import { isFunction, get, forEach } from 'lodash';
import 'intersection-observer';
import { isVisible } from 'visibility-behavior';
const BEHAVIOR_NAME = 'lazyRender';
const DEFAULT_ANCESTOR_TO_OBSERVE = 'body';

const findClosestEl = (el, targetSelector) => {
	if (isFunction(el.closest)) return el.closest(targetSelector);
	// polyfill for IE - source: https://developer.mozilla.org/en-US/docs/Web/API/Element/closest#Polyfill
	do {
		if (el.matches(targetSelector)) return el;
		el = el.parentElement || el.parentNode;
	} while (el !== null && el.nodeType === 1);
	return null;
};

const getTargetEl = (el, ancestorToObserve) => {
	return findClosestEl(el, ancestorToObserve) || el;
};

export function isInViewport({ top, offset, _window = window } = {}) {
	return top >= 0 && top <= _window.innerHeight + offset;
}

const isElInViewport = (el, offset) => {
	const rect = el.getBoundingClientRect();
	return (
		isVisible(rect) &&
		isInViewport({
			top: rect.top,
			offset
		})
	);
};

const elementToObserver = new WeakMap();

const observeElement = (keyElement, targetEl, cb) => {
	const observer = new IntersectionObserver(entries => {
		forEach(entries, entry => {
			if (entry.isIntersecting) {
				observer.disconnect();
				cb();
				return false;
			}
		});
	});
	observer.observe(targetEl);
	elementToObserver.set(keyElement, observer);
};

const unobserveElement = keyElement => {
	const observer = elementToObserver.get(keyElement);
	if (isFunction(get(observer, 'disconnect'))) {
		observer.disconnect();
	}
};

export default {
	name: BEHAVIOR_NAME,
	initialState: {
		ready: false
	},
	actionHandlers: {
		ELEMENT_READY: {
			stopPropagation: true,
			...createUpdateStateEffect(state => ({
				...state,
				behaviors: {
					...state.behaviors,
					[BEHAVIOR_NAME]: {
						...state.behaviors[BEHAVIOR_NAME],
						ready: true
					}
				}
			}))
		}
	},
	onConnect(
		el,
		dispatch,
		{
			ancestorToObserve = DEFAULT_ANCESTOR_TO_OBSERVE,
			offset = 50,
			timeout = 2000
		}
	) {
		const render = () => dispatch('ELEMENT_READY');
		if (isElInViewport(el, offset)) {
			render();
		} else {
			observeElement(el, getTargetEl(el, ancestorToObserve), () => {
				if (isElInViewport(el, offset)) {
					render();
				} else if (window.requestIdleCallback) {
					window.requestIdleCallback(render, { timeout });
				} else {
					setTimeout(render, timeout);
				}
			});
		}
	},
	onDisconnect: unobserveElement
};
