import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { preWsConfigSysId } from 'sn-uxpage-presource';
import '@servicenow/now-button';
import { isNil, toString, get, omitBy } from 'lodash';
import 'sn-component-visualization';
import { t } from 'sn-translate';
import lazyRenderBehavior from './lazy-render-behavior';
import { renderFiltersInfo, getFiltersInfo } from './filters-info';
import styles from './styles.scss';

import { ITEM_SELECTED } from './constants';
import { transformUUID } from './helpers/transform-uuid';

const actionPayloads = {
	btnRefresh: { btnId: 'btnRefresh' }
};

const safelyResolveSysId = (
	reportId,
	reportLegacySysId,
	paWidgetLegacySysId,
	reportSysId,
	paWidgetSysId
) => {
	return (
		reportId ||
		reportLegacySysId ||
		paWidgetLegacySysId ||
		reportSysId ||
		paWidgetSysId
	);
};

const view = (state, dispatch) => {
	const {
		showRefresh,
		behaviors: { lazyRender },
		properties: {
			label,
			variant,
			border,
			refreshInterval,
			reportSysId,
			paWidgetSysId,
			reportType,
			reportid: reportId,
			report: reportLegacySysId,
			pa_widget: paWidgetLegacySysId,
			isRealtime,
			kpiDetailsNavigation,
			refreshFrequency,
			chartType,
			showRealtimeIndicator,
			dataConfigurations,
			styleConfigurations,
			filterConfigurations,
			hideHeader,
			hideFiltersInfo
		}
	} = state;

	const refreshMilis = refreshInterval && refreshInterval * 60000;
	const onTitleEnter = () => dispatch('TITLE_ENTER');
	const onTitleLeave = () => dispatch('TITLE_LEAVE');
	const disableKpiDetailsOnClick =
		kpiDetailsNavigation && JSON.parse(kpiDetailsNavigation); // u can get a mixed type false/"false"

	const resolvedSysId = safelyResolveSysId(
		reportId,
		reportLegacySysId,
		paWidgetLegacySysId,
		reportSysId,
		paWidgetSysId
	);

	const formattedLabel = toString(label);
	const visualizationType = reportType || chartType; // maintain support for the depracated reportType prop

	const parsedDataConfigurations =
		dataConfigurations && JSON.parse(dataConfigurations);
	const isAdvancedScore =
		visualizationType === 'single_score' &&
		get(parsedDataConfigurations, '[0].include_sparkline');

	return (
		<div
			on-focusin={onTitleEnter}
			on-mouseenter={onTitleEnter}
			on-focusout={onTitleLeave}
			on-mouseleave={onTitleLeave}
			class={{
				'sn-component-card': true,
				'has-border': border === true || border === 'true',
				[`sn-component-card-${variant && variant.toLowerCase()}`]: variant,
				[visualizationType]: visualizationType,
				'advanced-score': isAdvancedScore,
				'no-header': hideHeader
			}}
		>
			{hideHeader ? null : (
				<div
					key="title-bar"
					class={{
						'title-bar': true,
						'has-title': label
					}}
				>
					{label ? (
						<h3 className="label-placeholder" title={formattedLabel}>
							{formattedLabel}
						</h3>
					) : (
						<div className="label-placeholder"></div>
					)}

					<now-button-iconic
						size="md"
						bare
						variant="tertiary"
						icon="sync-outline"
						class={{
							visible: showRefresh,
							'sn-component-card-refresh': true
						}}
						config-aria={{
							'aria-label': t('Refresh {0}', label || t('content'))
						}}
						appendToPayload={actionPayloads.btnRefresh}
					/>

					{!hideFiltersInfo ? renderFiltersInfo(state.filtersInfo) : null}
				</div>
			)}

			{lazyRender.ready ? (
				<div className="content">
					<sn-component-visualization
						forceRefresh={state.forceRefresh}
						sys-id={resolvedSysId}
						variant={variant}
						refresh-interval={refreshMilis}
						auto-click-behavior
						show-realtime-indicator={showRealtimeIndicator}
						aria-live={showRefresh ? 'polite' : 'off'}
						is-realtime={isRealtime}
						chartType={chartType}
						dataConfigurations={dataConfigurations}
						styleConfigurations={styleConfigurations}
						filterConfigurations={filterConfigurations}
						kpi-details-navigation={!disableKpiDetailsOnClick} // we need to negate this for sn-comp-viz
						refresh-frequency={refreshFrequency}
					/>
				</div>
			) : null}
		</div>
	);
};

const handleDrilldown = ({ action, dispatch, state, properties }) => {
	const disableKpiDetailsOnClick =
		properties.kpiDetailsNavigation &&
		JSON.parse(properties.kpiDetailsNavigation);

	// Disable drilldown when canClick is false
	if (
		!get(properties.styleConfigurations, 'canClick', true) ||
		disableKpiDetailsOnClick
	)
		return;

	const { payload } = action;
	const title =
		properties.label || payload.title || payload.label || payload.table || '';

	let params;
	let rendererType;

	// Send different params in case the event is coming from a reporting or PA chart
	if (payload.table) {
		rendererType = 'layout';
		params = {
			listTitle: title,
			table: payload.table,
			query: payload.conditions,
			view: payload.view,
			columns: payload.columns,
			workspaceConfigId: get(state, 'behaviors.wsConfigSysId'),
			disableQuickEdit: true
		};
	} else {
		rendererType = 'sn-kpi-details-renderer';
		params = {
			...transformUUID(payload.uuid),
			scoreDate: payload.scoreDate
		};
	}

	params = omitBy(params, isNil);
	dispatch(ITEM_SELECTED, {
		title,
		params,
		type: payload.type,
		renderer_type: rendererType
	});
};

const TAG_NAME = 'now-uxf-visualization-connected';
/**
 *  Connected component to visualize data from Reports and PA widgets
 *
 * ```jsx
 * const options =
 *  {
 *   'chartType': 'line',
 *	  'dataConfigurations': [...],
 *    'styleConfigurations': {...},
 *    'variant': 'SMALL'
 *  }
 *
 * <now-uxf-visualization-connected {...options} />
 * ```
 *
 * @seismicElement now-uxf-visualization-connected
 * @summary Connected component to visualize data from Reports and PA widgets.
 */
createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [
		preWsConfigSysId,
		{
			behavior: lazyRenderBehavior,
			options: {
				ancestorToObserve: 'now-uxf-page'
			}
		}
	],
	properties: {
		/**
		 * The label which is displayed in the header of the chart
		 *
		 * @type {string}
		 */
		label: { default: '', schema: { type: 'string' } },
		/**
		 * The size of the chart.
		 *
		 * @type {('SMALL'|'MEDIUM'|'LARGE'|'XL')}
		 */
		variant: {
			default: 'AUTO'
		},
		/**
		 * Sys Id of the report which should be displayed.
		 *
		 * @type {string}
		 * */
		reportSysId: {},
		/**
		 * Sys Id of the PA widget which should be displayed.
		 *
		 * @type {string}
		 */
		paWidgetSysId: {},
		/**
		 * [DEPRECATED]
		 * Sys Id of the PA widget which should be displayed.
		 *
		 * @type {string}
		 * */
		pa_widget: {},
		/**
		 * [DEPRECATED]
		 * Sys Id of the report which should be displayed.
		 *
		 * @type {string}
		 * */
		report: {},
		/**
		 * [DEPRECATED]
		 * Sys Id of the report which should be displayed.
		 *
		 * @type {string}
		 * */
		reportid: {},
		/**
		 * [DEPRECATED]
		 * Type of the visualization.
		 *
		 * @type {string}
		 * */
		reportType: {},
		/**
		 * When true, a border is displayed around the chart
		 *
		 * @type {boolean}
		 * */
		border: { default: true },
		/**
		 * Interval after which the chart is refreshed, when the chart is visible again.
		 *
		 * @type {number}
		 * */
		refreshInterval: {},
		/**
		 * When true, single score charts are updated in realtime
		 *
		 * @type {boolean}
		 * */
		isRealtime: { default: false },
		/**
		 * If enabled the visual indicator will be displayed when real time updates happen
		 * @type {Boolean}
		 */
		showRealtimeIndicator: {
			default: true
		},
		/**
		 * When true, user will not be redirected to KPI Details, on click
		 *
		 * @type {boolean}
		 * */
		kpiDetailsNavigation: {},
		/**
		 * Interval, in seconds, after which the chart is refreshed
		 *
		 * @type {number}
		 * */
		refreshFrequency: { default: '0' },
		/**
		 * Type of the visualization which should be displayed.
		 *
		 * @type {('pie'|'donut'|'semi_donut'|'line'|'column'|'area'|'spline'|'step_line'|'single_score'|'list')}
		 * */
		chartType: {},
		/**
		 * Array of data configuration settings you can set to configure your data retrieval.
		 *
		 *
		 * @type {PAGroupByDataConfiguration[]|ReportingGroupByDataConfiguration[]|PATimeSeriesDataConfiguration[]|ReportingTimeSeriesDataConfiguration[]|PASingleScoreDataConfiguration[]|ReportingSingleScoreDataConfiguration[]}
		 * */
		dataConfigurations: {},
		/**
		 * Used for additional styling configuration.
		 *
		 * Check the documentation of the following for components for all of the properties which can be configured:
		 *
		 * Time Series chart - https://developer.servicenow.com/dev.do#!/reference/libraries/paris/now-components/now-chart-timeseries/api
		 * Bar Chart - https://developer.servicenow.com/dev.do#!/reference/libraries/paris/now-components/now-chart-bar/api
		 * Pie/Donut/Semi-donut - https://developer.servicenow.com/dev.do#!/reference/libraries/paris/now-components/now-chart-donut-pie/api
		 * Single Score Advanced - https://developer.servicenow.com/dev.do#!/reference/libraries/paris/now-components/now-score-advanced/api
		 * Single Score Basic - https://developer.servicenow.com/dev.do#!/reference/libraries/paris/now-components/now-score-basic/api
		 *
		 * Example for a donut chart:
		 * ```
		 * {
		 *	 dataLabelConfig: {
		 *		 showTotal: true
		 *	 },
		 *	 hideRegister: false,
		 *	 registerConfig: {
		 *	 	 legendOnly: false
		 *	 },
		 *	 registerPosition: 'bottom',
		 *	 accessibilityMode: false,
		 *	 accessibilityTable: false
		 * }
		 * ```
		 *
		 * @type {{}}
		 * */
		styleConfigurations: {
			default: {},
			schema: {
				type: 'object'
			}
		},
		/**
		 * Supports filtering for one filter impls
		 **/
		filterConfigurations: {},
		/**
		 * When true, chart will listen for unified filters values and apply them to visualizations.
		 *
		 * @type {boolean}
		 */
		followFilters: {},
		/**
		 * When true, the filters info button will be hidden.
		 *
		 * @type {boolean}
		 */
		hideFiltersInfo: { default: false },
		/**
		 * When true, the header and its elements (label, refresh button and filter icon) will be hidden.
		 *
		 * @type {boolean}
		 * */
		hideHeader: { default: false }
	},
	initialState: {
		forceRefresh: false,
		showRefresh: false
	},
	actionHandlers: {
		'SN_VISUALIZATION#POINT_CLICKED': handleDrilldown,
		'SN_VISUALIZATION#LIST_VIEW_ALL_CLICKED': handleDrilldown,
		'SN_VISUALIZATION#CHARTDATA_LOADED': coeffects => {
			coeffects.updateState({
				filtersInfo: getFiltersInfo(
					coeffects.action.payload,
					coeffects.properties.filterConfigurations
				),
				forceRefresh: false
			});
		},
		TITLE_ENTER: coeffects => {
			coeffects.action.stopPropagation();
			coeffects.updateState({ showRefresh: true });
		},
		TITLE_LEAVE: coeffects => {
			coeffects.action.stopPropagation();
			coeffects.updateState({ showRefresh: false });
		},
		'NOW_BUTTON_ICONIC#CLICKED': coeffects => {
			if (coeffects.action.payload.btnId === actionPayloads.btnRefresh.btnId) {
				coeffects.action.stopPropagation();
				coeffects.updateState({ forceRefresh: true });
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the user clicks on a part of the chart
		 *
		 * @type {ItemSelectedPayload}
		 */
		ITEM_SELECTED: {}
	},
	styles
});

export default TAG_NAME;

/**
 * As there are no known good reliable way in runtime to get version of a component (kudos to TECTONIC team)
 * this is a custom manually crafted version timestamp like in prehistoric times.
 */
try {
	// eslint-disable-next-line global-require,import/no-unresolved
	const versionInfo = require('../tools/current-version-info');

	window.PAR_COMPONENTS_INFO = window.PAR_COMPONENTS_INFO || {};
	window.PAR_COMPONENTS_INFO.UxfVisualizationConnected = versionInfo;
} catch (ex) {
	/** Suppress module not found error in case version file was not generated during the build */
}
