import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';

import styles from './filters-info-view.scss';

/**
 * This is an internal component to use by now-uxf-visualization-connected.
 * It renders UI for the content of filter information popover.
 */
createCustomElement('sn-uxf-visualization-filter-info-popover', {
	properties: {
		filtersInfo: {}
	},
	styles,
	view: ({
		properties: {
			filtersInfo: { applied, notApplied }
		}
	}) => {
		function renderList({ header, items, icon, iconClass }) {
			if (!Array.isArray(items) || items.length === 0) {
				return undefined;
			}
			return (
				<Fragment>
					<p className="section-header">{header}</p>
					<ul>
						{items.map(x => (
							<li>
								<now-icon icon={icon} className={iconClass} title={x.reason} />{' '}
								<span className="filter-label">{x.label}</span>
							</li>
						))}
					</ul>
				</Fragment>
			);
		}

		return (
			<div className="filters-info-popover-content">
				{renderList({
					header: t('Filters applied'),
					items: applied,
					icon: 'circle-check-outline',
					iconClass: 'icon-applied'
				})}
				{renderList({
					header: t('Filters not applied'),
					items: notApplied,
					icon: 'circle-close-outline',
					iconClass: 'icon-not-applied'
				})}
			</div>
		);
	}
});

const filterPopoverPositions = [
	{ target: 'bottom-end', content: 'top-end' },
	{ target: 'top-center', content: 'bottom-center' },
	{ target: 'bottom-center', content: 'top-center' },
	{ target: 'center-end', content: 'center-start' },
	{ target: 'center-start', content: 'center-end' }
];

export function renderFiltersInfo(filtersInfo) {
	if (!filtersInfo) return null;

	const { applied, showIcon, showAppliedCount } = filtersInfo;
	if (!showIcon) {
		return undefined;
	}

	return (
		<now-popover positions={filterPopoverPositions}>
			<div slot="trigger" className="filters-popover-toggle-container">
				{showAppliedCount ? (
					<now-badge
						size="sm"
						status="info"
						variant="secondary"
						value={applied.length}
					/>
				) : (
					undefined
				)}
				<now-button-iconic
					size="md"
					bare
					variant="tertiary"
					icon="filter-outline"
					className="filters-icon"
					config-aria={{
						'aria-label': t('Show applied filter information')
					}}
				/>
			</div>
			<sn-uxf-visualization-filter-info-popover
				slot="content"
				aria-live="polite"
				filtersInfo={filtersInfo}
			/>
		</now-popover>
	);
}
